import React from "react";
import { Box, makeStyles, Typography, Paper, Switch } from "@material-ui/core";
import { useHistory, useLocation } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  paperBox: {
    padding: "50px",
    [theme.breakpoints.down("sm")]: {
      padding: "20px",
    },
  },
  twoFaAuth: {
    cursor: "pointer",
    "& h5": {
      color: "rgba(61, 61, 61, 1)",
    },
  },
  googleAuthentication: {
    borderBottom: "1px solid #80808052",
    marginBottom: "16px",
  },
  textheadBox: {
    paddingBottom: "9px",
    borderBottom: "1px solid #80808052",
    "& h2": {
      fontSize: "40px",
      color: "rgb(243, 109, 54)",
    },
  },
  displaySpaceBetween: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
}));

const AntSwitch = withStyles((theme) => ({
  root: {
    width: 28,
    height: 16,
    padding: 0,
    display: "flex",
  },
  switchBase: {
    padding: 2,
    color: theme.palette.grey[500],
    "&$checked": {
      transform: "translateX(12px)",
      color: theme.palette.common.white,
      "& + $track": {
        opacity: 1,
        backgroundColor: theme.palette.primary.main,
        borderColor: theme.palette.primary.main,
      },
    },
  },
  thumb: {
    width: 12,
    height: 12,
    boxShadow: "none",
  },
  track: {
    border: `1px solid ${theme.palette.grey[500]}`,
    borderRadius: 16 / 2,
    opacity: 1,
    backgroundColor: theme.palette.common.white,
  },
  checked: {},
}))(Switch);

export default function ChangePassword() {
  const history = useHistory();
  const classes = useStyles();
  const location = useLocation();

  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
    checkedC: true,
  });

  const handleGoogleAuthChange = (event) => {
    setState({ ...state, checkedC: event.target.checked });
  };

  const handleEmailAuthChange = (event) => {
    setState({ ...state, checkedB: event.target.checked });
  };

  return (
    <Paper elevation={2} className={classes.paperBox}>
      <Box className={classes.textheadBox}>
        <Box className="displayStart">
          <img
            onClick={() => history.goBack()}
            src="/images/arrowback.svg"
            width="35px"
            style={{ cursor: "pointer" }}
            alt="Go Back"
          />
          &nbsp;&nbsp;
          <Typography variant="h2" style={{ color: "rgba(243, 109, 54, 1)" }}>
            Two Factor Authentication
          </Typography>
        </Box>
      </Box>
      <Box mt={3} className={classes.twoFaAuth}>
        <Box
          className={`${classes.googleAuthentication} ${classes.displaySpaceBetween}`}
        >
          <Typography variant="h5">Google Authentication</Typography>
          <AntSwitch
            checked={state.checkedC}
            onChange={handleGoogleAuthChange}
            name="checkedC"
          />
        </Box>
        <Box className={`displaySpaceBetween`}>
          <Typography variant="h5">Email Authentication</Typography>
          <AntSwitch
            checked={state.checkedB}
            onChange={handleEmailAuthChange}
            name="checkedB"
          />
        </Box>
      </Box>
    </Paper>
  );
}
